* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #ece9e6;
  background: -webkit-linear-gradient(to right, #fff, #ece9e6);
  background: linear-gradient(to right, #fff, #ece9e6);
  justify-content: center;
  align-items: center;
  font-family: Poppins, sans-serif;
  display: flex;
  position: relative;
}

.mobile-container {
  width: 420px;
  background-color: #fff;
  border-radius: 5px;
  margin-top: 5rem;
  overflow: hidden;
  box-shadow: 0 0 10px 2px #0000001a;
}

.header {
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
}

header input {
  background-color: #eee;
  border: none;
  padding: .2rem;
}

header input:focus {
  outline: none;
}

.header button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  margin-left: 5px;
  font-size: 1rem;
}

.fav-container {
  background: #f4c4f3;
  background: -webkit-linear-gradient(to right, #f5cdf4, #e5d5e5);
  background: linear-gradient(to right, #f5cdf4, #e5d5e5);
  padding: 1rem;
}

.fav-container h3 {
  text-align: center;
}

.fav-container ul {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.fav-container li {
  width: 75px;
  text-align: center;
  margin: 4px 10px;
  list-style: none;
  position: relative;
}

.fav-container li .clear {
  opacity: 0;
  position: absolute;
  top: -.5rem;
  right: -.5rem;
}

.fav-container li:hover .clear {
  opacity: 1;
}

.fav-container li span {
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 75px;
  font-size: .9rem;
  display: inline-block;
  overflow: hidden;
}

.fav-container ul li img {
  width: 75px;
  object-fit: cover;
  border: 1.5px solid #fff;
  border-radius: 50%;
}

.lower-block {
  padding: 1.3rem;
}

.random-recipe {
  position: relative;
}

.random {
  background-color: #fff;
  position: absolute;
  top: 25px;
  left: 0;
}

.random-recipe img {
  width: 100%;
  object-fit: cover;
  height: 240px;
}

.footer {
  justify-content: space-between;
  align-items: center;
  margin-top: -6px;
  padding: 1rem;
  display: flex;
  box-shadow: 0 0 10px 2px #0000001a;
}

.heart-btn {
  color: #d3c8c8;
  background: none;
  border: none;
  font-size: 1.3rem;
}

.heart-btn.active {
  color: #b928b9;
}

.popup-container {
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
  overflow: auto;
}

.popup-info {
  max-width: 500px;
  width: 100%;
  background-color: #fff;
  border-radius: 5px;
  padding: 0 2rem;
  position: absolute;
  top: 0;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}

.popup-info h2 {
  text-align: center;
  padding-top: 2rem;
}

.popup-info img {
  max-width: 100%;
}

.popup-info ul {
  list-style: none;
}

.close-btn {
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: absolute;
  top: .5rem;
  right: .5rem;
}

.close-popup {
  font-size: 1.1rem;
}

